import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import * as Yup from "yup";
import axios from "../../axios";
import React, { Component } from "react";
import { Formik, Form, Field } from "formik";
import {
  Button,
  LinearProgress,
  Grid,
  FormControl,
  InputLabel,
  MenuItem,
} from "@material-ui/core";
import { TextField, Select } from "formik-material-ui";
import { connect } from "react-redux";

import { DateTimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import { now } from "moment";

const mapStateToProps = (state) => ({
  collegeID: state.collegeID,
});
class NewEvent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDate: new Date(now()),
      file: [],
      eventCatList: [],
      selectedEventCat: "",
    };
  }
  handleDateChange(date) {
    this.setState({
      selectedDate: new Date(date).toISOString(),
    });
  }
  componentDidMount() {
    axios
      .get(`api/eventscat/list/` + this.props.collegeID, {})
      .then((res) => {
        this.setState({
          eventCatList: res.data,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  render() {
    const handleChangeEventCat = (event) => {
      let id = event.target.value;
      this.setState({
        selectedEventCat: id,
      });
    };

    async function uploadFile(file1, callback) {
      let bodyFormData = new FormData();
      bodyFormData.append("file", file1);
      const request = axios
        .post("upload", bodyFormData, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          //alert(res.data.fileName)
          let filena = res.data.fileName;
          request.then(() => callback(filena));
          request.catch((err) => callback(err.response));
        });
    }

    const fileSelected = (event) => {
      this.setState({
        file: event.target.files[0],
      });
    };
    return (
      <Grid spacing={2} lg={12} xs={12}>
        <Grid item lg={12} xs={12}>
          <h1 class="card-title">Add New College Event(s) </h1>
        </Grid>

        <Formik
          initialValues={{
            EventCatID: this.state.selectedEventCat,
            event_name: "",
            event_reg_link: "",
            event_desc: "",
            filename: "",
            date: this.state.selectedDate,
          }}
          validationSchema={Yup.object({
            event_name: Yup.string()
              .max(200, "Must be 200 characters or less")
              .min(3, "Must be at least 3 characters")
              .required("Event Title Can't be Blank"),
            event_desc: Yup.string()
              .min(10, "Must be at least 3 characters")
              .required("Event Description Can't be Blank"),
          })}
          onSubmit={(values, { setSubmitting, resetForm }) => {
            let fn = "";
            uploadFile(this.state.file, (response) => {
              // your code after API response
              fn = response;
              setTimeout(() => {
                setSubmitting(false);
                values.EventCatID = this.state.selectedEventCat;
                values.date = this.state.selectedDate;
                values.filename = fn;
                axios
                  .post("api/newevent/" + this.props.collegeID, values)
                  .then((res) => {
                    if (res.data.flag === "success") {
                      alert("The Event Added Successfully");
                    }
                  })
                  .catch((error) => {
                    alert(error);
                    resetForm();
                  });
                resetForm();
                this.setState({
                  file: [],
                });
              }, 500);
            });
          }}
        >
          {({ submitForm, isSubmitting }) => (
            <Form>
              <Grid item lg={12} md={6}>
                <div class="formspacer " />
                <FormControl variant="outlined" fullWidth required>
                  <InputLabel htmlFor="selectedEventCat">
                    Select Event Catagory
                  </InputLabel>
                  <Field
                    component={Select}
                    name="selectedEventCat"
                    label="Select Event Catagory"
                    onChange={handleChangeEventCat}
                  >
                    {this.state.eventCatList.map((eventCat) => (
                      <MenuItem key={eventCat.id} value={eventCat.id}>
                        {eventCat.category}
                      </MenuItem>
                    ))}
                  </Field>
                </FormControl>
              </Grid>

              <Grid lg={12} xs={12}>
                <div class="formspacer " />
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <DateTimePicker
                    value={this.state.selectedDate}
                    onChange={(date) => this.handleDateChange(date)}
                    label="Select Event Date & Time"
                    inputVariant="outlined"
                    fullWidth
                    format="dd/MM/yyyy hh:mm a"
                  />
                </MuiPickersUtilsProvider>

                <div class="formspacer " />
              </Grid>
              <Grid item lg={12} md={12}>
                <Field
                  component={TextField}
                  name="event_name"
                  type="text"
                  label="Event Title"
                  variant="outlined"
                  fullWidth
                  required
                />
              </Grid>
              <Grid item lg={12} md={12}>
                <div class="formspacer " />
                <Field
                  component={TextField}
                  name="event_reg_link"
                  type="text"
                  label="Event Registration Link"
                  variant="outlined"
                  fullWidth
                  required
                />
              </Grid>
              <Grid item lg={12} md={12}>
                <div class="formspacer " />
                <div class="formspacer " />
                <Field
                  component={TextField}
                  name="event_desc"
                  type="text"
                  label="Event Description"
                  variant="standard"
                  fullWidth
                  required
                  multiline={true}
                  maxRows={20}
                  minRows={10}
                />
              </Grid>

              <Grid item lg={12} xs={12}>
                <div class="formspacer " />
                <span
                  style={{
                    color: "#fff",
                    padding: "9px",
                    backgroundColor: "#b00",
                    marginRight: "20px",
                  }}
                >
                  Choose Display Photo in JPG/PNG/GIF format
                </span>

                <input
                  onChange={fileSelected}
                  type="file"
                  accept="image/png, image/gif, image/jpeg"
                ></input>
              </Grid>

              <Grid item lg={12} xs={12}>
                <div class="formspacer " />
                {isSubmitting && <LinearProgress />}
                <div class="formspacer " />
                <Button
                  variant="contained"
                  color="primary"
                  disabled={isSubmitting}
                  onClick={submitForm}
                  fullWidth
                >
                  Add New Event
                </Button>
              </Grid>
            </Form>
          )}
        </Formik>
      </Grid>
    );
  }
}

export default connect(mapStateToProps)(NewEvent);
